<template>
  <div class="register">
    <img class="logo" src="@/assets/images/logo-footer.svg" alt />
    <el-form
      :model="loginModel"
      :rules="rules"
      ref="loginForm"
      label-width="100px"
      class="psi-register-form"
      label-position="top"
    >
      <el-form-item prop>
        <h1 class="title">PCSBox Web</h1>
      </el-form-item>
      <el-form-item prop>
        <h1 class="subtitle">欢迎使用PCSBox</h1>
      </el-form-item>
      <el-form-item prop="username">
        <el-input
          v-model="loginModel.username"
          placeholder="设置登录账号"
          autocomplete="new-password"
          class="login-input"
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          type="password"
          class="login-input"
          v-model="loginModel.password"
          autocomplete="new-password"
          placeholder="设置登录密码"
        ></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="register" class="login-button" :loading="logining">下一步</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
const sha256 = require('js-sha256').sha256
export default {
  data() {
    return {
      logining: false,
      loginModel: {
        client_id: 'client',
        client_secret: 'secret',
        grant_type: 'password',
        username: '',
        password: '',
      },
      rules: {
        username: [
          {
            validator: this.Utils.checkAccount.bind(this),
            trigger: ['change', 'blur'],
          },
        ],
        password: [
          {
            validator: this.Utils.checkPassword.bind(this),
            trigger: ['blur'],
          },
        ],
      },
    }
  },
  methods: {
    register() {
      let _this = this
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.logining = true
          let _password = this.loginModel.password
          this.ApiService.postRegister({
            username: this.loginModel.username,
            password: sha256(this.loginModel.password),
          }).then(
            (res) => {
              this.logining = false
              this.Utils.message(res.message, 'success')
              this.$router.push({
                path: '/login',
              })
            },
            (err) => {
              this.Utils.message('注册失败', 'warning')
              this.logining = false
            }
          )
        } else {
          return false
        }
      })
    },
  },
  mounted() {},
}
</script>

<style lang="less" scoped>
.register {
  width: 100%;
  height: 100%;
  min-height: 700px;
  background-image: url('../assets/images/register.png');
  display: flex;
  align-items: center;
  justify-content: center;
  .logo {
    position: absolute;
    height: 60px;
    left: 50px;
    top: 50px;
  }
  .title {
    margin-top: 43px;
    color: #ffffff;
    font-size: 100px;
    text-align: center;
    font-weight: bold;
  }
  .subtitle {
    color: #ffffff;
    font-size: 40px;
    text-align: center;
    margin: 16px 0 72px 0px;
  }
}
</style>
<style lang="less">
.psi-register-form {
  .el-form-item__label {
    display: block;
    height: 30px;
    line-height: 1;
    padding: 0 0 12px;
  }
  .login-input {
    .el-input__inner {
      width: 600px;
      height: 60px;
      opacity: 0.9;
      background: #ffffff;
      border: 1px solid #53566f;
      box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);
      border-radius: 5px;
      border-radius: 5px;
      font-size: 20px;
    }
    input::-webkit-input-placeholder {
      /*WebKit browsers*/
      opacity: 0.5;
      font-family: PingFangSC;
      font-weight: bold;
      font-size: 20px;
      color: #333333;
      letter-spacing: 0;
    }
  }
  .login-button,
  .login-button:hover,
  .login-button:focus {
    width: 600px;
    height: 60px;
    background: #eaecfa !important;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    border-radius: 5px;
    margin-top: 72px;
    span {
      font-size: 20px;
      color: #3246d3;
      letter-spacing: 0;
      text-align: center;
    }
  }
}
</style>
