import { Message } from 'element-ui'
// 封装消息提示
function message(message, type, duration) {

    if (arguments.length == 0) {
        return
    }
    if (arguments.length == 1) {
        Message({
            showClose: true,
            message: message,
            offset: 50
        })
        return
    }
    if (arguments.length == 2) {
        Message({
            showClose: true,
            message: message,
            type: type,
            offset: 50
        })
        return
    }
    Message({
        showClose: true,
        message: message,
        type: type,
        duration: duration,
        offset: 50
    })
}

function checkAccount(rule, value, callback) {
    if (!value) {
        return callback(new Error('用户名不能为空'));
    }
    let reg = /^[a-zA-Z0-9_]{0,14}$/
    if (!reg.test(value)) {
        return callback(new Error('请输入正确的用户名'));
    }
    let patrn = /^[0-9]*$/;

    if (patrn.test(value)) {
        return callback(new Error('不能为纯数字'));
    }
    return callback()
}

function checkPassword(rule, value, callback) {
    if (!value) {
        return callback(new Error('密码不能为空'));
    }

    if (value.length < 4) {
        return callback(new Error('密码至少4位'));
    }
    return callback()
}

function checkIp(rule, value, callback) {
    if (!value) {
        return callback(new Error('ip不能为空'));
    }
    let reg = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/

    if (!reg.test(value)) {
        return callback(new Error('请输入正确的ip'));
    }
    return callback()
}
const utils = {
    message: message,
    checkIp: checkIp,
    checkAccount: checkAccount,
    checkPassword: checkPassword
}
export default utils