<template>
  <div id="barchart" style="width:100%;height:100%;"></div>
</template>

<script>
// 引入 echarts 主模块。
import * as echarts from "echarts/lib/echarts";
// 引入折线图。
import "echarts/lib/chart/bar";
// 引入提示框组件、标题组件、工具箱组件。
import "echarts/lib/component/tooltip";
// import "echarts/lib/component/legend";
import "echarts/lib/component/title";
import "echarts/lib/component/toolbox";

export default {
  props: {
    chartData: Object,
  },
  data() {
    return {
      myChart: {},
    };
  },
  methods: {
    drawChart() {
      this.myChart.setOption({
        color: ["#4558DA", "#E3EAEF"],
        title: {
          text: "柱状图",
          textStyle: {
            color: "#141B60",
            fontSize: 16,
          },
        },
        // legend: {
        //   left: "left",
        //   top: 30,
        //   data: ["sender", "receiver"],
        // },
        tooltip: {},
        xAxis: {
          boundaryGap: true,
          type: "category",
          data: this.chartData.date,
          axisLine: {
            lineStyle: {
              color: "#D6D6D6",
            },
          },

          axisTick: {
            lineStyle: {
              color: "#D6D6D6",
            },
            alignWithLabel: true,
          },
        },
        yAxis: {
          type: "value",

          splitLine: {
            lineStyle: {
              color: ["#F5F5F5"],
            },
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: ["#DFE2E9"],
            },
          },
          axisTick: {
            show: false,
          },
        },
        grid: {
          left: 25,
          top: 80,
          right: 30,
          bottom: 20,
        },
        series: [
          {
            name: "sender",
            type: "bar",
            barWidth: 10,
            itemStyle: {
              normal: {
                barBorderRadius: [5, 5, 0, 0],
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#3549D4" }, //柱图渐变色

                  { offset: 1, color: "#A6B1FE" }, //柱图渐变色
                ]),
              },
              emphasis: {
                //悬浮的渐变色在这里
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#3549D4" }, //柱图渐变色

                  { offset: 1, color: "#A6B1FE" }, //柱图渐变色
                ]),
              },
            },

            data: this.chartData.data.senderBarData,
          },
          {
            name: "receiver",
            type: "bar",
            barWidth: 10,
            itemStyle: {
              normal: {
                barBorderRadius: [5, 5, 0, 0],
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#E3EAEF" }, //柱图渐变色
                  { offset: 1, color: "#E3EAEF" }, //柱图渐变色
                ]),
              },
              emphasis: {
                //悬浮的渐变色在这里
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#E3EAEF" }, //柱图渐变色
                  { offset: 1, color: "#E3EAEF" }, //柱图渐变色
                ]),
              },
            },
            data: this.chartData.data.receiverBarData,
          },
        ],
      });
    },
  },
  watch: {
    chartData: {
      deep: true,
      handler() {
        this.drawChart();
      },
    },
  },
  mounted() {
    this.myChart = echarts.init(document.getElementById("barchart"));
    this.drawChart();
  },
};
</script>

