import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import login from '../views/login.vue'
import register from '../views/register.vue'
Vue.use(VueRouter)

const routes = [{
    path: '/index',
    name: 'Home',
    component: Home,

},
{
    path: '/login',
    name: 'login',
    component: login
},
{
    path: '/identity',
    name: 'identity',
    component: () =>
        import( /* webpackChunkName: "identity" */ '../views/identityManage/index.vue')
},
{
    path: '/account',
    name: 'account',
    component: () =>
        import( /* webpackChunkName: "account" */ '../views/accountingManage/index.vue')
},
{
    path: '/currency',
    name: 'currencySetting',
    component: () =>
        import( /* webpackChunkName: "setting" */ '../views/setting/currencySetting.vue')
},
{
    path: '/job',
    name: 'jobSetting',
    component: () =>
        import( /* webpackChunkName: "jobSetting" */ '../views/setting/jobSetting.vue')
},
{
    path: '/task',
    name: 'task',
    component: () =>
        import( /* webpackChunkName: "task" */ '../views/taskManage/index.vue')
},
{
    path: '/task/start',
    name: 'start',
    component: () =>
        import( /* webpackChunkName: "task" */ '../views/taskManage/startCompute.vue'),
},
{
    path: '/task/detail/:taskId',
    name: 'detail',
    component: () =>
        import( /* webpackChunkName: "task" */ '../views/taskManage/detail.vue'),
},
// register.vue
{
    path: '/register',
    name: 'register',
    component: register,
}
]

const router = new VueRouter({
    mode: 'history',
    routes
})
// router.beforeEach((to, from, next) => {
