<template>
  <div id="linechart" style="width:100%;height:100%;"></div>
</template>

<script>
// 引入 echarts 主模块。
import * as echarts from "echarts/lib/echarts";
// 引入折线图。
import "echarts/lib/chart/line";
// 引入提示框组件、标题组件、工具箱组件。
import "echarts/lib/component/tooltip";
import "echarts/lib/component/legend";
import "echarts/lib/component/title";
import "echarts/lib/component/toolbox";

export default {
  props: {
    chartData: Object,
  },
  data() {
    return {
      myChart: {},
    };
  },
  methods: {
    drawChart() {
      this.myChart.setOption({
        color: ["#5B8FF9", "#5AD8A6"],
        title: {
          text: "多折线图",
          textStyle: {
            color: "#141B60",
            fontSize: 16,
          },
        },
        legend: {
          left: "left",
          top: 30,
          data: ["sender", "receiver"],
        },
        tooltip: {},
        xAxis: {
          boundaryGap: true,
          type: "category",
          data: this.chartData.date,
          axisLine: {
            lineStyle: {
              color: "#D6D6D6",
            },
          },

          axisTick: {
            lineStyle: {
              color: "#D6D6D6",
            },
            alignWithLabel: true,
          },
        },
        yAxis: {
          type: "value",

          splitLine: {
            lineStyle: {
              color: ["#F5F5F5"],
            },
          },
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
        },
        grid: {
          left: 25,
          top: 80,
          right: 30,
          bottom: 20,
        },
        series: [
          {
            name: "sender",
            type: "line",
            smooth: true,
            data: this.chartData.data.senderLineData,
          },
          {
            name: "receiver",
            type: "line",
            smooth: true,
            data: this.chartData.data.receiverLineData,
          },
        ],
      });
    },
  },
  watch: {
    chartData: {
      deep: true,
      handler() {
        this.drawChart();
      },
    },
  },
  mounted() {
    this.myChart = echarts.init(document.getElementById("linechart"));
    this.drawChart();
  },
};
</script>

