<template>
  <div id="app">
    <!-- <leftMenu v-if="$route.name!=='login'&&$route.name!=='register'" class="left-menu" /> -->
    <div class="content" v-if="$route.name!=='login'&&$route.name!=='register'">
      <div class="header">
        <span style="cursor: pointer;" @click="logout" v-if="userInfo">注销</span>
        <span style="cursor: pointer;" @click="login" v-if="!userInfo">登陆</span>
      </div>
      <div class="wrapper">
        <router-view />
      </div>
      <div class="footor">© 2020 PCSBox Web. All Rights Reserved.</div>
    </div>
    <router-view v-if="$route.name==='login'||$route.name==='register'" />
  </div>
</template>
<script>
import leftMenu from '@/components/leftMenu'
export default {
  components: {
    leftMenu,
  },
  data() {
    return {
      userInfo: '',
    }
  },

  methods: {
    login() {
      this.$router.push({
        path: '/login',
      })
    },
    logout() {
      this.userInfo = null
      localStorage.removeItem('userInfo')
      this.$router.push({
        path: '/login',
      })
    },

    getActiveStatus() {
      if (!localStorage.getItem('userInfo')) {
        this.Utils.message('请登录！', 'warning')
        this.$router.push({
          path: '/login',
        })
      }
      // this.ApiService.getActiveStatus().then((res) => {
      //   // if (!res.data) {
      //   //   this.$router.push({
      //   //     path: '/register',
      //   //   })
      //   // } else {
      //   //   if (!localStorage.getItem('userInfo')) {
      //   //     this.Utils.message('请登录！', 'warning')
      //   //     this.$router.push({
      //   //       path: '/login',
      //   //     })
      //   //   }
      //   // }

      // })
    },
  },
  mounted() {
    this.$root.$on('loginSuccess', () => {
      this.userInfo = localStorage.getItem('userInfo')
    })
    this.getActiveStatus()
    if (window.location.hash) {
      this.$router.replace({
        path: '/' + window.location.hash.replace('#', ''),
      })
    }
  },
  created() {
    this.userInfo = localStorage.getItem('userInfo')
  },
}
</script>
<style>
#app {
  height: 100%;
  width: 100%;
  display: table;
}
</style>
<style lang="less" scoped>
.left-menu {
  float: left;
  height: 100%;
}
.content {
  display: flex;
  flex-direction: column;
  background-color: #eee;
  float: right;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  // width: calc(100% - 250px);
  .header {
    height: 70px;
    line-height: 70px;
    background: #ffffff;
    width: 100%;
    text-align: right;
    padding-right: 20px;
    font-size: 14px;
    color: #697e8d;
    font-weight: bold;
  }
  .wrapper {
    width: calc(100% - 40px);
    margin: 20px;
    flex: 1;
  }
  .footor {
    background: #ffffff;
    color: #c2c8d5;
    letter-spacing: 0.43px;
    text-align: center;
    height: 50px;
    font-weight: bold;
    line-height: 50px;
  }
}
</style>
