import Http from 'axios'
import API from './api-path'
import Utils from "../assets/js/utils"
Http.defaults.headers.post['Content-Type'] = 'application/json;charset=utf-8'
// 设置axios为form-data
// Http.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
// Http.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded';

/**
 * encode get 请求对象
 * @param params
 * @returns {string}
 */
const encodeParams = params => {
    const r = '?'
    const p = []
    for (const key in params) {
        p.push(`${encodeURIComponent(key)}=${encodeURIComponent(params[key])}`)
    }
    return r + p.join('&')
}

/**
 * 请求类
 */
class ApiService {
    constructor() {

        this.postLogin = this.login.bind(this, API.ApiUrl.postLogin)

        // postRegister
        this.postRegister = this.post.bind(this, API.ApiUrl.postRegister)
        this.getHomeData = this.get.bind(this, API.ApiUrl.getHomeData)
        this.getActiveStatus = this.get.bind(this, API.ApiUrl.getActiveStatus)

        //认证列表
        this.getAuthList = this.get.bind(this, API.ApiUrl.getAuthList)
        //发起认证
        this.postAuthStart = this.post.bind(this, API.ApiUrl.postAuthStart)
        //发起计算
        this.startComputed = this.post.bind(this, API.ApiUrl.startComputed)
        // 审核
        this.postAudit = this.post.bind(this, API.ApiUrl.postAudit)
        // 任务列表
        this.getTaskList = this.get.bind(this, API.ApiUrl.getTaskList)
        // //选择标签反馈
        this.postAuthLabel = this.post.bind(this, API.ApiUrl.postAuthLabel)
        //合作方列表
        this.getCompanyList = this.get.bind(this, API.ApiUrl.getCompanyList)
        this.getTaskDetail = this.get.bind(this, API.ApiUrl.getTaskDetail)
        this.getConfig = this.get.bind(this, API.ApiUrl.getConfig)
        // 保存配置
        this.postConfig = this.post.bind(this, API.ApiUrl.getConfig)
        // // 任务搜索
        // this.getSearchList = this.get.bind(this, API.ApiUrl.getSearchList)
        this.getOptions = this.get.bind(this, API.ApiUrl.getOptions)
        this.postComputeAudit = this.post.bind(this, API.ApiUrl.postComputeAudit)
        this.postAuthAuto = this.post.bind(this, API.ApiUrl.postAuthAuto)
        this.getCompanyInfo = this.get.bind(this, API.ApiUrl.getCompanyInfo)
        this.recharge = this.post.bind(this, API.ApiUrl.recharge)

        // //获取文件目录
        // this.getFileList = this.get.bind(this, API.ApiUrl.getFileList)
        this.interceptorsOfReq()
        this.interceptorsOfRes()
    }

    /**
     * get请求
     * @param url
     * @param params
     * @returns {Promise.<TResult>}
     */
    get(url, params) {
        if (params) {
            url += encodeParams(params)
        }
        let _this = this
        return Http.get(url).then(res => res.data, err => { })
    }

    /**
     * post请求
     * @param url       请求地址
     * @param params    请求参数
     * @param flag      是否需要加签名
     * @returns {Promise.<TResult>}
     */
    post(url, params) {
        if (!params) {
            params = {}
        }
        return Http.post(url, params).then(res => res.data)
    }
    login(url, params) {
        if (!params) {
            params = {}
        }
        return Http.post(url,'', { params: params }).then(res => res.data)
    }
    /**
     * 请求拦截器
     */
    interceptorsOfReq() {
        return Http.interceptors.request.use(
            config => {
                // 如果需要token验证, 头部带上token
                // config.headers.token = token
                let token = localStorage.getItem("userInfo") && JSON.parse(localStorage.getItem("userInfo")).token
                //user/active
                if (token && config.url.indexOf('user/active') === -1 && config.url.indexOf('user/register') === -1) {
                    config.headers.Authorization = `Bearer ${token}`;
                }
                // if (config.url.indexOf('oauth/token') > -1) {
                //     config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
                //     config.transformRequest = [function (data) {
                //         let ret = ''
                //         for (let it in data) {
                //             ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
                //         }
                //         return ret
                //     }]
                // }
                if (config.url.indexOf('charge/recharge') > -1) {
                    config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
                }
                return config
            },
            err => {
                return Promise.reject(err)
            }
        )
    }

    /**
     * 响应拦截器
     */
    interceptorsOfRes() {
        let _this = this
        Http.interceptors.response.use(
            response => {
                return response
            },
            function (error) {
                if (error.response && error.response.status == 401) {
                    // var timer = setTimeout(() => {
                    //     Utils.message("登录失效，请重新登录", "warning")
                    //     _this.router.push({
                    //         path: "/login"
                    //     })
                    // }, 1000)

                }
                return Promise.reject(error)
            }
        )
    }
}

export default new ApiService()