<template>
  <div class="home">
    <div class="home-top">
      <div class="top-block bgfff">
        <span class="sub-title" v-if="dataObj.remainTimeSeconds">可使用截止时间</span>
        <span class="sub-title" v-if="dataObj.remainCount">剩余计算次数</span>
        <div>
          <span
            class="end-date"
            v-if="dataObj.remainTimeSeconds"
          >{{dataObj.remainTimeSeconds?moment(dataObj.remainTimeSeconds).format('YYYY.MM.DD'):'-- '}}</span>

          <span class="end-times" v-if="dataObj.remainCount">{{dataObj.remainCount}}</span>
          <span class="end-time" v-if="dataObj.remainCount">次</span>
        </div>
        <span class="block-bottom">Since last month</span>
      </div>
      <div class="top-block bgfff">
        <span class="sub-title">平均计算耗时</span>
        <div>
          <span class="end-date">{{getAvgCostTimeSeconds[0]}}</span>
          <span class="end-time">h</span>
          <span class="end-times">{{getAvgCostTimeSeconds[1]}}</span>
          <span class="end-time">m</span>
          <span class="end-date">{{getAvgCostTimeSeconds[2]}}</span>
          <span class="end-time">s</span>
        </div>
        <span class="block-bottom">Since last month</span>
      </div>
      <div class="top-block bgfff">
        <span class="sub-title">激活时间</span>
        <div>
          <span
            class="end-date"
            style="margin-right:10px"
          >{{moment(dataObj.activeTime).format('YYYY.MM.DD')}}</span>
          <span class="end-time">{{moment(dataObj.activeTime).format('HH:mm:ss')}}</span>
        </div>
        <span class="block-bottom">Since last month</span>
      </div>
      <div class="top-block bgfff">
        <span class="sub-title">数据条数</span>
        <div>
          <span class="end-date">{{dataObj.dataCount||"--"}}</span>
        </div>
        <span class="block-bottom">All time</span>
      </div>
    </div>

    <div class="home-middle bgfff">
      <lineChart :chartData="lineData" />
    </div>

    <div class="home-bottom">
      <div class="bgfff">
        <pieChart :chartData="pieData" />
      </div>
      <div class="bgfff">
        <barChart :chartData="barChart" />
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import lineChart from '../components/linchart'
import pieChart from '../components/pieChart'
import barChart from '../components/barChart'
export default {
  name: 'Home',
  components: { lineChart, pieChart, barChart },
  computed: {
    getAvgCostTimeSeconds() {
      const sec = this.dataObj.avgCostTimeSeconds || 0
      const h = Math.floor(Number(sec) / 3600)
      const m = Math.floor((Number(sec) - h * 3600) / 60)
      const s = Number(sec) % 60
      return [h, m, s]
    }
  },
  data() {
    return {
      lineData: {
        date: [],
        data: {
          senderLineData: [],
          receiverLineData: []
        }
      },
      pieData: {
        data: [],
        legendData: []
      },
      barChart: {
        date: [],
        data: {
          senderBarData: [],
          receiverBarData: []
        }
      },
      dataObj: {}
    }
  },
  methods: {
    getBaseInfo() {
      this.ApiService.getHomeData().then(
        res => {
          this.dataObj = res.data
          let lineDate = []
          //line状图
          res.data.senderLineData.forEach(element => {
            lineDate.push(element.x)
            this.lineData.data.senderLineData.push(element.y)
          })
          res.data.receiverLineData.forEach(element => {
            lineDate.push(element.x)
            this.lineData.data.receiverLineData.push(element.y)
          })
          this.lineData.date = Array.from(new Set(lineDate))
          //饼图
          res.data.pieChartData.forEach(element => {
            this.pieData.data.push({
              value: element.y,
              name: element.x
            })
            this.pieData.legendData.push(element.x)
          })
          // bar
          let barDate = []
          res.data.senderHistogramData.forEach(element => {
            barDate.push(element.x)
            this.barChart.data.senderBarData.push(element.y)
          })
          res.data.receiverHistogramData.forEach(element => {
            barDate.push(element.x)
            this.barChart.data.receiverBarData.push(element.y)
          })
          this.barChart.date = Array.from(new Set(barDate))
        },
        err => {}
      )
    }
  },
  mounted() {
    this.getBaseInfo()
  }
}
</script>
<style lang="less" scoped>
.home {
  .home-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .top-block {
      height: 180px;
      margin-right: 20px;
      width: 25%;
      padding: 30px 20px 20px 20px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      &:nth-of-type(4) {
        margin-right: 0px;
      }
      white-space: pre-wrap;
      .sub-title {
        font-size: 14px;
        font-weight: bold;
        color: #353c77;
      }
      .end-date {
        font-size: 18px;
        color: #3145d1;
        font-weight: bold;
      }
      .end-time {
        font-size: 12px;
        color: #3145d1;
        font-weight: bold;
        margin-right: 5px;
      }
      .end-times {
        font-size: 18px;
        color: #3145d1;
        font-weight: bold;
      }
      .block-bottom {
        color: #dde0e8;
        font-size: 12px;
      }
    }
  }
  .home-middle {
    padding: 20px;
    margin: 20px 0px;
    width: 100%;
    height: 250px;
  }
  .home-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    & > div {
      height: 280px;
      padding: 20px;
      &:nth-of-type(1) {
        width: 40%;
        margin-right: 20px;
      }
      &:nth-of-type(2) {
        flex: 1;
      }
    }
  }
}
</style>
