<template>
  <div id="piechart" style="width:100%;height:100%;"></div>
</template>

<script>
// 引入 echarts 主模块。
import * as echarts from "echarts/lib/echarts";
// 引入折线图。
import "echarts/lib/chart/pie";
// 引入提示框组件、标题组件、工具箱组件。
import "echarts/lib/component/tooltip";
import "echarts/lib/component/legend";
import "echarts/lib/component/title";
import "echarts/lib/component/toolbox";

export default {
  props: {
    chartData: Object,
  },
  data() {
    return {
      myChart: {},
    };
  },
  methods: {
    drawChart() {
      this.myChart.setOption({
        color: ["#5B8FF9", "#5AD8A6", "#EB7E65", "#7585A2", "#F7C739"],
        title: {
          text: "环形图",
          textStyle: {
            color: "#141B60",
            fontSize: 16,
          },
        },
        legend: {
          bottom: "bottom",
          icon: "circle",
          data: this.chartData.legendData,
          textStyle: {
            fontSize: 12,
          },
        },
        tooltip: {},

        grid: {},
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: ["40%", "65%"],
            hoverAnimation: false,
            avoidLabelOverlap: false,
            label: {
              show: false,
              position: "center",
            },
            label: {
              show: true,
              formatter: "{c}",
            },
            labelLine: {
              show: true,
              length: 10,
              length2: 0,
            },

            data: this.chartData.data,
          },
        ],
      });
    },
  },
  watch: {
    chartData: {
      deep: true,
      handler() {
        this.drawChart();
      },
    },
  },
  mounted() {
    this.myChart = echarts.init(document.getElementById("piechart"));
    this.drawChart();
  },
};
</script>

