<template>
  <div class="login">
    <img class="logo" src="@/assets/images/logo-footer.svg" alt />
    <el-form
      :model="loginModel"
      :rules="rules"
      ref="loginForm"
      label-width="100px"
      class="psi-login-form"
      label-position="top"
    >
      <el-form-item prop>
        <h1 class="title">PCSBox Web</h1>
      </el-form-item>
      <el-form-item prop>
        <h1 class="subtitle">隐私计算让数据像资产一样流动</h1>
      </el-form-item>
      <el-form-item prop="username">
        <!-- <span class="login-label" slot="label">账号:</span> -->
        <el-input
          v-model="loginModel.username"
          placeholder="请输入用户名"
          autocomplete="new-password"
          class="login-input"
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <!-- <span class="login-label" slot="label">密码:</span> -->
        <el-input
          type="password"
          class="login-input"
          v-model="loginModel.password"
          autocomplete="new-password"
          placeholder="请输入密码"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-checkbox-group v-model="rememberMe" class="login-check">
          <el-checkbox label="记住密码" name="type"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="login" class="login-button" :loading="logining">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
const sha256 = require('js-sha256').sha256
export default {
  data() {
    return {
      logining: false,
      loginModel: {
        client_id: 'client',
        client_secret: 'secret',
        grant_type: 'password',
        username: '',
        password: '',
      },
      rememberMe: '',
      rules: {
        username: [
          {
            validator: this.Utils.checkAccount.bind(this),
            trigger: ['change', 'blur'],
          },
        ],
        password: [
          {
            validator: this.Utils.checkPassword.bind(this),
            trigger: ['blur'],
          },
        ],
      },
    }
  },
  methods: {
    login() {
      let _this = this
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.logining = true
          let _password = this.loginModel.password
          this.ApiService.postLogin({
            // client_id: 'client',
            // client_secret: 'secret',
            // grant_type: 'password',
            // password: sha256(this.loginModel.password),
            username: this.loginModel.username,
            password: this.loginModel.password,
          }).then(
            (res) => {
              localStorage.setItem(
                'userInfo',
                JSON.stringify({
                  name: _this.loginModel.username,
                  token: res.data,
                })
              )
              if (_this.rememberMe) {
                localStorage.setItem(
                  'asdfsd',
                  btoa(
                    JSON.stringify({
                      username: _this.loginModel.username,
                      ps: _password,
                    })
                  )
                )
              }
              _this.logining = false
              _this.$root.$emit('loginSuccess')
              // _this.$router.push({
              //   path: '/',
              // })
              window.location.href="/"
            },
            (err) => {
              _this.Utils.message('登录失败', 'warning')
              _this.logining = false
            }
          )
        } else {
          return false
        }
      })
    },
  },
  mounted() {
    let infos = localStorage.getItem('asdfsd')
    if (infos) {
      this.loginModel.username = JSON.parse(atob(infos)).username
      this.loginModel.password = JSON.parse(atob(infos)).ps
    }
  },
}
</script>

<style lang="less" scoped>
.login {
  width: 100%;
  height: 100%;
  min-height: 700px;
  background-image: url('../assets/images/loginBg.png');
  display: flex;
  align-items: center;
  justify-content: center;
  .logo {
    position: absolute;
    height: 60px;
    left: 50px;
    top: 50px;
  }
  .title {
    margin-top: 43px;
    color: #ffffff;
    font-size: 100px;
    text-align: center;
    font-weight: bold;
  }
  .subtitle {
    color: #ffffff;
    font-size: 40px;
    text-align: center;
    margin: 16px 0 72px 0px;
  }
}
</style>
<style lang="less">
.psi-login-form {
  .el-form-item__label {
    display: block;
    height: 30px;
    line-height: 1;
    padding: 0 0 12px;
  }
  .login-input {
    .el-input__inner {
      width: 600px;
      height: 60px;
      opacity: 0.9;
      background: #20233b;
      border: 1px solid #53566f;
      box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);
      border-radius: 5px;
      border-radius: 5px;
      font-size: 20px;
      color: #ffffff;
    }
    input::-webkit-input-placeholder {
      /*WebKit browsers*/
      opacity: 0.5;
      font-family: PingFangSC;
      font-weight: bold;
      font-size: 20px;
      color: #ffffff;
      letter-spacing: 0;
    }
  }
  .login-button,
  .login-button:hover {
    width: 600px;
    height: 60px;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    border-radius: 5px;
    span {
      font-size: 20px;
      color: #ffffff;
      letter-spacing: 0;
      text-align: center;
    }
  }
  .login-check {
    text-align: left;
    line-height: 1;
    margin-bottom: 37px;
    .el-checkbox__label {
      font-size: 12px;
      padding-left: 4px;
      color: #7f90b1;
      letter-spacing: 0;
    }
    .el-checkbox {
      line-height: 1;
    }
  }
}
</style>
